<template>
  <!-- eslint-disable -->
  <div class="page2">
    <div class="pt-4 pb-3 top-header">
      <h4 class="text-white text-center">
        Ready for Challenge
      </h4>
      <h4 class="text-white text-center mt-2">
        {{ user.username }}
      </h4>
    </div>
    <div class="camera-area">
      <video ref="video" class="videoPlayer" id="video" autoplay />
    </div>
    <div class="bottom-footer">
      <div class="capture-btn mx-auto mb-2" @click="$router.push('/page-3')" />
      <p class="text-center text-white">Get on the Yoga Mat and Start</p>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { mapGetters } from "vuex";
export default {
  data: () => ({
    constraints: {
      video: {
        facingMode: "user",
        width: 1280,
        height: 720,
      },
      audio: false,
    },
    videoEl: null,
  }),

  computed: {
    ...mapGetters({
      user: "user",
    }),
  },

  methods: {
    async startCamera() {
      try {
        let stream = await navigator.mediaDevices.getUserMedia(
          this.constraints
        );
        this.videoEl = document.getElementById("video");
        this.videoEl.srcObject = stream;
      } catch (err) {
        console.error("Oops. Something is broken.", err.message);
      }
    },

    async stopCamera() {
      let stream = this.videoEl.srcObject;
      let tracks = stream.getTracks();
      // eslint-disable-next-line
      tracks.forEach((track) => {
        track.stop();
      });
      this.videoEl.srcObject = null;
      return;
    },
  },
  async created() {
    await this.startCamera();
  },

  async destroyed() {
    await this.stopCamera();
  },
};
</script>

